var Simple = require("./protobuf");
const c2c = {
	'message msg': {
		'optional string _id': 1,
		'optional uInt32 type': 2, //
		'optional uInt32 fromUserId': 3,//userId
		'optional uInt32 toUserId': 4,//userId
		'optional uInt32 groupId': 5,//id,Id,Id,Id
		'optional uInt32 affairId': 6,//Id
		'optional uInt32 toRoleId': 7,//roleId
		'optional uInt32 fromRoleId': 8,//roleId
		'optional uInt32 sub': 9,//
		'optional string name': 10,//
		'optional string content': 11,//
		'optional uInt32 time': 12,//,
		'optional uInt32 index': 13,//index
        'optional string apns': 14,//@roleId json
        'optional string _key': 15,//
        'optional uInt32 state': 16//
	},

	'message c2c': {
		'optional uInt32 type': 1,
		'optional string requestId': 2,
		'optional msg chat': 5,//
		'optional string params': 3,//,json
		'repeated msg msgList': 4,//
		'optional string data': 6,//stringjson.stringify
	}
}

var protos = Simple.parse(c2c);
Simple.init({encoderProtos: protos, decoderProtos: protos});

exports.encodeChat = function (o) {
	return Simple.encode("message msg", o);
}


exports.decodeChat = function (buff) {
	return Simple.decode("message msg", buff);
}

exports.encodeC2c = function (o) {
	return Simple.encode("message c2c", o);
}

exports.decodeC2c = function (buff) {
	return Simple.decode("message c2c", buff);
}