var io = require('engine.io-client');
var proto = require("./proto/proto");
var Constants = require("./constants");
var Common = require("./common");
var store = require("./msg-cache");
var serverService = require('./service/serverService');
var cachingRequests = new Map();
var _socket, _address, _userId;
var timer;
var pingInterval;
var pingTimer;
var pingTimeoutCount = 0;
var lastActiveTime = new Date().getTime();
var _handler = {};
var _token;
var _config = {pingInterval: 3 * 60 * 1000, reconnectInterval: 10 * 1000 ,requestTimeout: 20000, debug: true};
var signed = false;
var _waitSends = [];
var reconnectInterval;



function sendPing() {
    var ping = {type: Constants.MSG_TYPE.PING};
    if(_socket){
        _socket.send(proto.encodeC2c(ping));
        pingTimer = setTimeout(function () {
            pingTimeoutCount++;
            if (pingTimeoutCount === 3) {
                _socket.close();
                serverService.getConnector(_userId).then(function (address) {
                    //TODO 
                    //
                    // connector
                    if(address){
                        _address = JSON.parse(address).data;
                        init(_address, _userId, _token, _config, _handler);
                    }
                }).catch(function (err) {
                    "use strict";
                    console.log(err)
                });

            }
        }, _config.pingInterval);
    }
}


/**
 * 
 * @param requestId id
 * @param success 
 * @param fail 
 * @param note 
 * @returns {null}
 */
var bindRequestHandler = function (requestId, success, fail, note) {
    if (!requestId) {
        return null;
    }
    var cache = {};
    cache.resolve = success;
    cache.reject = fail;

    setTimeout(function () {
        var c = cachingRequests.get(requestId);
        if (c) {
            c.reject(note + ' timeout requestId:'+requestId);
            delete cachingRequests.delete(requestId);
        }
    }, _config.requestTimeout);

    cachingRequests.set(requestId, cache);
};


function sendToChannel(data) {
    if (signed) {
        _socket.send(data);
    } else {
        _waitSends.push(data);
    }
}

function init(address, userId, token, config, handler) {
    handler || (handler = {});
    for (var key in handler){
        if (handler.hasOwnProperty(key)) {
            for(var index in handler[key]){
                addHandler(key, handler[key][index]);
            }
        }
    }
    if (config) {
        config.debug != undefined && (_config.debug = config.debug);
        config.requestTimeout != undefined && (_config.requestTimeout = config.requestTimeout);
        config.pingInterval != undefined && (_config.pingInterval = config.pingInterval);
    }
    _address = address;
    _userId = userId;
    _token = token;
    signed = false;


    if (timer) {
        clearInterval(timer);
    }
    _socket = new io("https://"+address, {
        transports:['websocket','polling'],
        rejectUnauthorized:false,
        secure:true
    });
    _socket.binaryType = "arraybuffer";

    _socket.on("open", function () {
        const requestId = Common.generateRequestId(userId);
        var params = {userId: userId, agent: "Web", token: token};
        var sign = {type: Constants.MSG_TYPE.SIGN_IN, params: JSON.stringify(params), requestId: requestId};
        var buff = proto.encodeC2c(sign);
        _socket.send(buff);
        bindRequestHandler(requestId, function (data) {
            execHandlers('SIGN',[data]);

            signed = true;
            clearInterval(reconnectInterval);

            _waitSends.forEach(function (data) {
                _socket.send(data);
            });
            _waitSends = [];
        }, function (err) {
            console.error(err);
        },"sign");
    });

    _socket.on("message", function (data) {
        var c2c;
        try {
            c2c = proto.decodeC2c(data);
        } catch (e) {
            console.error(e);
            return;
        }

        switch (c2c.type) {
            case Constants.MSG_TYPE.REDIRECT:
                console.log('redirect to '+c2c.data);
                _socket.close();
                init(c2c.data, userId, token ,config);
                break;

            case Constants.MSG_TYPE.MSG:
                execHandlers('MSG',[c2c.chat]);
                lastActiveTime = new Date().getTime();
                store.receive(c2c.chat, roomQueryMsg);
                break;

            case Constants.MSG_TYPE.RESPONSE:
                var cache = cachingRequests.get(c2c.requestId);
                if (!cache) {
                    return;
                }
                cachingRequests.delete(c2c.requestId);
                cache.resolve(c2c);
                break;

            case Constants.MSG_TYPE.BUMPED:
                execHandlers('BUMP');
                clearInterval(pingInterval);
                _socket.close();
                break;

            case Constants.MSG_TYPE.SYSTEM:
                execHandlers('NOTICE', [JSON.parse(c2c.data)]);
                break;

            case Constants.MSG_TYPE.ERROR:
                var c = cachingRequests.get(c2c.requestId);
                if (!c) {
                    return;
                }
                cachingRequests.delete(c2c.requestId);
                c.reject(c2c.data);
                break;

            case Constants.MSG_TYPE.NEW_LOGIN:
                //_handler.onNewLogin && _handler.onNewLogin();
                break;

            case Constants.MSG_TYPE.PONG:
                if (pingTimer) {
                    clearTimeout(pingTimer);
                }
                break;


            default:
                console.log(c2c.type);
                break;

        }


    });


    _socket.on('close', function (data) {
        execHandlers('CLOSE');
        console.log('socket close reason: ',data);
        if(data==='transport error' || data==='transport close'){
            console.log('lost connector! try reconnecting...');
            signed = false;
            reconnect();
        }else if(!signed){
            console.log('socket sign out!');
        }else {
            console.log('redirect or bumped!');
        }
    });

}

function signOut() {
    signed = false;
    _socket.close();
}

function reconnect() {
    clearInterval(reconnectInterval);
    reconnectInterval = setInterval(function () {
        if(signed === false){
            serverService.getConnector(_userId).then(function (address) {
                console.log(_userId, " "+address);
                if(address){
                    init(address, _userId, _token, _config);
                }
            }).catch(function (err) {
                "use strict";
                console.log(err);
                console.log("reconnect failed, try reconnect again...")
            });
        }
    },_config.reconnectInterval);
}



var getUnreadCount = function(chat, success, fail) {
    var requestId = Common.generateRequestId(_userId);
    var data = {chat: chat, type: Constants.MSG_TYPE.GET_UNREAD_COUNT,requestId:requestId};
    sendToChannel(proto.encodeC2c(data));
    bindRequestHandler(requestId, success, fail,"getUnreadCount")
}

var markRead = function (data) {
    sendToChannel(proto.encodeC2c(data));
}

var roomQueryMsg = function(chat, params, success, fail) {
    var requestId = Common.generateRequestId(_userId);
    var data = {chat: chat, params: JSON.stringify(params), type: Constants.MSG_TYPE.ROOM_MSG_QUERY,requestId:requestId};
    sendToChannel(proto.encodeC2c(data));
    bindRequestHandler(requestId, success, fail,"roomQueryMsg");
}


/**
 * 
 * @param data
 * @param success
 * @param fail
 */
exports.sendMsg = function (data, success, fail) {

    var requestId = Common.generateRequestId(_userId);
    var c2c = {type: Constants.MSG_TYPE.MSG, chat: data, requestId: requestId};
    var encode = proto.encodeC2c(c2c);
    sendToChannel(encode);

    bindRequestHandler(requestId, function (c2c) {//
        data._id = requestId;
        data._key = Common.generateKey(data);
        data.index = c2c.chat.index;
        data.time = c2c.chat.time;
        data.state = "success";
        success&&success(data);
    }, function () {
        data.state = "fail";
        fail&&fail(data);
    },'send msg');

    data._id = requestId;
    data.state = "sending";
    return store.send(data);
}

/**
 * 
 * @param data
 * @param success
 * @param fail
 */
exports.notify = function (data, success, fail) {
    var requestId = Common.generateRequestId(_userId);
    var c2c = {type: Constants.MSG_TYPE.SYSTEM, chat: data, requestId: requestId};
    var encode = proto.encodeC2c(c2c);
    sendToChannel(encode);

    bindRequestHandler(requestId, function (c2c) {//
        data._id = requestId;
        data.time = c2c.chat.time;
        data.state = "success";
        success&&success(data);
    }, function (err) {
        data.state = "fail";
        fail&&fail(data);
        console.log(err);
    }, 'send system msg');

    data._id = requestId;
    data.state = "sending";
    return;
}

var addHandler = function (key, handler) {
    if (!isValidHandler(handler)) {
        throw new TypeError('handler must be a function');
    }

    var handlers = _handler[key];
    var listenerIsWrapped = typeof handler === 'object';

    if (indexOfHandlers(handlers, handler) === -1) {
        if(!_handler[key]){
            _handler[key] = [];
        }
        _handler[key].push(listenerIsWrapped ? handler : {
            handler: handler,
            once: false
        });
    }

    return this;
};

var addOnceHandler = function (key, handler) {
    return addHandler(key, {
        handler: handler,
        once: true
    });
}

var removeHandler = function (key, handler) {
    var handlers = _handler[key];
    var index;

    index = indexOfHandlers(handlers, handler);
    if (index !== -1) {
        handlers.splice(index, 1);
    }

    return this;
}

var execHandlers = function (key, args) {
    var handlers = _handler[key];
    if(handlers){
        var index = handlers.length;
        while (index--){
            handlers[index].handler && handlers[index].handler.apply(this, args);
            if(handlers[index].once === true){
                removeHandler(key, handlers[index].handler);
            }
        }
    }
}


function isValidHandler (handler) {
    if (typeof handler === 'function' || handler instanceof RegExp) {
        return true
    } else if (handler && typeof handler === 'object') {
        return isValidHandler(handler.handler)
    } else {
        return false
    }
}

function indexOfHandlers(handlers, handler) {
    if(handlers){
        var i = handlers.length;
        while (i--) {
            if (handlers[i].handler === handler) {
                return i;
            }
        }
    }
    return -1;
}



/**
 *
 * @param address 
 * @param userId userId
 * @param token  token
 * @param handler {onMsg:, onSign:,onBumped:web,
 * onNotice:onNewLogin}
 * @param config  {pingInterval:3*60*1000,requestTimeout:2000,debug:true}
 */
exports.init = init;
exports.signOut = signOut;
exports.getUnreadCount = getUnreadCount;
exports.markRead = markRead;
exports.roomQueryMsg = roomQueryMsg;

exports.addOnceHandler = addOnceHandler;
exports.addHandler = addHandler;
exports.removeHandler = removeHandler;


//check heart beat
pingInterval = setInterval(function () {
    if (new Date().getTime() - lastActiveTime > (_config.pingInterval / 2)) {
        sendPing();
    }
}, _config.pingInterval);


