var client = require("../client-core");
var Common = require("../common");
var Constants = require("../constants");

exports.notify = function (roleId,groupId,sub,name,content,success,fail) {
    var notifyMsg = {
        type        :   Constants.CHAT_TYPE.GROUP,
        fromRoleId  :   roleId,
        name        :   name,
        content     :   content,
        groupId     :   groupId,
        sub         :   sub
    };
    client.notify(notifyMsg,success,fail);
}
