var client = require("../client-core");
var store = require("../msg-cache");
var Common = require("../common");
var Constants = require("../constants");

/**
 * 
 * @param params {roleId, toRoleId, affairId, limit}
 * @param success
 * @param fail
 */
exports.openPrivateChat = function (params,success,fail) {
    var loadParams = {
        session         :   Common.generatePrivateChatSession(params.roleId,params.toRoleId,params.affairId),
        roomQueryMsg    :   client.roomQueryMsg,
        limit           :   params.limit
    };

    store.loadMoreMsg(loadParams, success, fail);
    markReadOfPrivateChat({
        roleId      :   params.roleId,
        toRoleId    :   params.toRoleId,
        affairId    :   params.affairId
    });
}

/**
 * 
 * @param params {roleId , toRoleId id, toUserId id, affairId id, sub , name , content }
 * @param success
 * @param fail
 */
exports.sendPrivateChatMsg = function (params,success,fail) {

    var chatMsg = {
        type        :   Constants.CHAT_TYPE.AFFAIR_CHAT,
        fromRoleId  :   params.fromRoleId,
        affairId    :   params.affairId,
        toRoleId    :   params.toRoleId,
        toUserId    :   params.toUserId,
        name        :   params.name,
        content     :   params.content,
        sub         :   params.sub
    };
    client.sendMsg(chatMsg,success,fail);
}

/**
 * 
 * @param params {roleId, toRoleId, affairId, endTime , limit}
 * @param success
 * @param fail
 */
exports.loadPrivateChatMsg = function (params, success, fail) {
    var loadParams = {
        session         :   Common.generatePrivateChatSession(params.roleId,params.toRoleId,params.affairId),
        roomQueryMsg    :   client.roomQueryMsg,
        endTime         :   params.endTime,
        limit           :   params.limit,
    };
    store.loadMoreMsg(loadParams, success, fail);
}

/**
 * 
 * @param params {roldId, toRoleId, affairId}
 */
exports.closePrivateChat = function (params) {
    var roomInfo = store.getRoomInfo(Common.generateGroupChatSession(params.roleId,params.toRoleId,params.affairId));
    roomInfo.open = false;
    markReadOfPrivateChat({
        roleId      :   params.roleId,
        toRoleId    :   params.toRoleId,
        affairId    :   affairId
    });
    return roomInfo;
}

/**
 * 
 * @param params {roleId, toRoleId, affairId}
 * @param success
 * @param fail
 */
exports.getUnreadCountOfPrivateChat = function (params, success, fail) {
    var session = Common.generatePrivateChatSession(params.roleId,params.toRoleId,params.affairId);
    client.getUnreadCount(session, success, fail);
}

/**
 * 
 * @param params {roleId, toRoleId, affairId}
 */
var markReadOfPrivateChat = function (params) {

    var session = Common.generatePrivateChatSession(params.roleId,params.toRoleId,params.affairId);
    var data = {
        chat    :   session,
        type    :   Constants.MSG_TYPE.MARK_READ_TIME
    };
    client.markRead(data);
}

exports.markReadOfPrivateChat = markReadOfPrivateChat;