var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789".split('');
var possibleLength = possible.length;
var Constants = require("./constants");
var https = require('https')
var http = require('http');
var querystring = require("querystring");

var longToString = function (n) {
    var text = "";
    var a;
    if(n==0){
        return 'A';
    }
    while (n>0) {
        a = n%possibleLength;
        n = parseInt(n/possibleLength);
        text += possible[a];
    }
    return text;
};

var generateId = function (id1,id2) {
    var max,min;
    if(id1>id2){
        max =id1;
        min = id2;
    }else{
        max = id2;
        min= id1;
    }
    return longToString(max)+"!"+longToString(min);
}


exports.generateRequestId = function(userId) {
    return longToString(userId)+"$"+ longToString(new Date().getTime());
}


exports.generateKeyOfGroup = function (groupId) {
    return longToString(groupId)+"$";
};

exports.generateKeyOfAffairChat = function (affairId, roleId,toRoleId) {
    return longToString(affairId) + "@" + generateId(roleId, toRoleId);
}

exports.generateKey = function (msg) {
    switch (msg.type){
        case Constants.CHAT_TYPE.GROUP:
            return this.generateKeyOfGroup(msg.groupId);
        case Constants.CHAT_TYPE.AFFAIR_CHAT:
            return this.generateKeyOfAffairChat(msg.affairId,msg.fromRoleId,msg.toRoleId);
    }
}

exports.generateChat = function (msg) {
    switch (msg.type){
        case Constants.CHAT_TYPE.GROUP:
            return  {fromRoleId:msg.fromRoleId,groupId:msg.groupId,type:Constants.CHAT_TYPE.GROUP};
        case Constants.CHAT_TYPE.AFFAIR_CHAT:
            return {fromRoleId:msg.fromRoleId,toRoleId:msg.toRoleId,affairId:msg.affairId,type:Constants.CHAT_TYPE.AFFAIR_CHAT};
    }
}

exports.generateGroupChatSession = function (fromRoleId,groupId) {
    return {groupId:groupId,type:Constants.CHAT_TYPE.GROUP,fromRoleId:fromRoleId};
}

exports.generatePrivateChatSession = function (fromRoleId,toRoleId,affairId) {
    return {fromRoleId:fromRoleId,toRoleId:toRoleId,affairId:affairId,type:Constants.CHAT_TYPE.AFFAIR_CHAT};
}

/**
 * httpRequestGET
 * @param options
 */
var httpsRequestGET = function(options){
    options.rejectUnauthorized = false;
    options.method = 'GET';

    return new Promise(function (resolve, reject) {

        var req = https.request(options, function (res) {

            res.setEncoding('utf8');
            res.on('data', function (chunk) {

                var data = {};
                data.statusCode = res.statusCode;
                data.headers = JSON.stringify(res.headers);
                data.body = chunk;
                resolve(data);
            });
        });

        req.on('error', function (e) {

            reject(e.message);
        });

        req.end();
    });

}

/**
 * httpRequestPOST
 * @param options
 * @param data
 */
var httpsRequestPOST = function(options, data){
    options.rejectUnauthorized = false;
    options.method = 'POST';

    return new Promise(function (resolve, reject) {

        var req = https.request(options, function (res) {

            res.setEncoding('utf8');
            res.on('data', function (chunk) {

                var data = {};
                data.statusCode = res.statusCode;
                data.headers = JSON.stringify(res.headers);
                data.body = chunk;
                resolve(data);
            });
        });

        req.write(data);

        req.on('error', function (e) {

            reject(e.message);
        });

        req.end();
    });

}

/**
 * https GET
 * @param options:Object{hostname:String, port:Number, path:String}
 */
exports.httpsGet = function(options){
    return new Promise(function (resolve, reject) {

        httpsRequestGET(options).then(function (data) {

            if(data.statusCode === 200){
                resolve(JSON.parse(data.body));
            }else if(data.statusCode === 401){
                reject("user no auth!");
            }else if(data.statusCode === 500){
                reject("server down!");
            }else {
                reject("unknown error! errMsg:"+JSON.stringify(data));
            }

        }).catch(function (err) {
            reject(err);
        });
    });
}

/**
 * https POST
 * @param options:Object{hostname:String, port:Number, path:String}
 * @para data:Object
 */
exports.httpsPOST = function(options, data){
    data = querystring.stringify(data);
    return new Promise(function (resolve, reject) {

        httpsRequestPOST(options, data).then(function (data) {

            if(data.statusCode === 200){
                resolve(JSON.parse(data.body));
            }else if(data.statusCode === 401){
                reject("user no auth!");
            }else if(data.statusCode === 500){
                reject("server down!");
            }else {
                reject("unknown error! errMsg:"+JSON.stringify(data));
            }

        }).catch(function (err) {
            reject(err);
        });
    });
}


/**
 * httpRequest
 * @param options
 */
exports.httpRequest = function(options) {

    return new Promise(function (resolve, reject) {

        var req = http.request(options, function (res) {

            res.setEncoding('utf8');
            res.on('data', function (chunk) {

                var data = {};
                data.statusCode = res.statusCode;
                data.headers = JSON.stringify(res.headers);
                data.body = chunk;
                resolve(data);
            });
        });

        req.on('error', function (e) {

            reject(e.message);
        });

        req.end();
    });
}
