var client = require("../client-core");
var store = require("../msg-cache");
var Common = require("../common");
var Constants = require("../constants");

/**
 * 
 * @param params {groupId, roleId, limit}
 * @param success
 * @param fail
 */
exports.openGroupChat = function (params, success, fail) {
    var loadParams = {
        session         :   Common.generateGroupChatSession(params.roleId,params.groupId),
        roomQueryMsg    :   client.roomQueryMsg,
        limit           :   params.limit
    };

    store.openRoom(loadParams, success, fail);
    this.markReadOfGroup({
        groupId     :   params.groupId,
        roleId      :   params.roleId
    });
}

/**
 * 
 * @param params{roleId ,groupId id,sub ,name ,content ,apns @, affairId id}
 * @param success
 * @param fail
 */
exports.sendGroupMsg = function (params, success, fail) {
    var chatMsg = {
        type        :   Constants.CHAT_TYPE.GROUP,
        fromRoleId  :   params.fromRoleId,
        name        :   params.name,
        content     :   params.content,
        groupId     :   params.groupId,
        sub         :   params.sub,
        apns        :   JSON.stringify(params.apns)
    };
    params.affairId && (chatMsg.affairId = params.affairId);
    return client.sendMsg(chatMsg,success,fail);
}

/**
 * 
 * @param params {groupId, roleId, endTime , limit}
 * @param success
 * @param fail
 */
exports.loadGroupMsg = function (params, success, fail) {
    var loadParams = {
        session         :   Common.generateGroupChatSession(params.roleId,params.groupId),
        roomQueryMsg    :   client.roomQueryMsg,
        endTime         :   params.endTime,
        limit           :   params.limit,
    };
    store.loadMoreMsg(loadParams, success, fail);
}

/**
 * 
 * @param params {groupId, roldId}
 */
exports.closeGroupChat = function (params) {
    var roomInfo = store.getRoomInfo(Common.generateGroupChatSession(params.roleId,params.groupId));
    roomInfo.open = false;
    this.markReadOfGroup({
        groupId     :   params.groupId,
        roleId      :   params.roleId
    });
    return roomInfo;
}

/**
 * 
 * @param params {groupId, roldId}
 * @param success
 * @param fail
 */
exports.getUnreadCountOfGroup = function (params, success, fail) {
    var session = Common.generateGroupChatSession(params.roleId,params.groupId);
    client.getUnreadCount(session, success, fail);
}

/**
 *  
 * @param params {groupId, roldId}
 */
exports.markReadOfGroup = function (params) {
    var chat = Common.generateGroupChatSession(params.roleId,params.groupId);
    var data = {
        chat    :   chat,
        type    :   Constants.MSG_TYPE.MARK_READ_TIME
    };
    client.markRead(data);
}



