exports.getConnectors = function () {
    return new Promise(function (resolve, reject) {
        var options = {
            hostname: Conf.BACKEND.HOST,
            port: Conf.BACKEND.PORT,
            path: Conf.PREFIX+'/server/connector',
            method: 'GET',
            headers: {
                'X-SIMU-RoleId': 0,
                'X-SIMU-AffairId': 0,
                'Authorization': "",
                "X-label": "gbc"
            }
        };

        httpRequest(options).then(function (address) {
            resolve(JSON.parse(address.body));
        }).catch(function (err) {
            reject(err);
        })
    });
}