var Common = require("./common");


var cache = {};//{sdsd$:{msgList:[],unread:10,open:false,pulling:false}}

//index time
var compare = function (v1,v2) {
    if(v1.time==v2.time){
        if(v1.index==v2.index){
            return 0;
        }
        return v1.index>v2.index?1:-1;
    }
    return v1.time>v2.time?1:-1;
}

function search(msg,array) {
    var high    = array.length;
    var low     = 0;
    while (high > low) {
        var index    = (high + low) / 2 >>> 0;
        var ordering = compare(array[index], msg);
        if (ordering < 0) low  = index + 1;
        else if (ordering > 0) high = index;
        else return index;
    }
    return -1;
}

function insert(msg,array) {
    if(search(msg,array)!=-1){//
        return;
    }
    var index = array.length;
    array.push(msg);
    while (index > 0) {
        var i = index, j = --index;
        if (compare(array[i], array[j]) < 0) {
            var temp = array[i];
            array[i] = array[j];
            array[j] = temp;
        }
    }
}

function isLocal(msg) {//
    if(msg){
        return msg.state == 'fail' || msg.state == 'sending';
    }else {
        return false;
    }
}

function last(list) {
    return list[list.length-1];
}


function insertHistoryMsgs(roomInfo, msgs) {
    var cachedMsg = roomInfo.msgList;
    if(msgs && msgs.length>0){
        msgs.forEach(function (msg) {
            insert(msg,cachedMsg);
        });
        if (!roomInfo.open) {
            roomInfo.unread += msgs.length;
        }
    }
}

/**
 * 
 * @param msg
 * @param roomQueryMsg 
 * @param callback
 */
var receive = function (msg, roomQueryMsg) {
    var id = Common.generateKey(msg);
    if (!cache[id]) {//
        return;
    }
    var roomInfo = cache[id];
    var msgList = roomInfo.msgList;
    var lastMsg = msgList[msgList.length - 1];

    //
    if (isLocal(lastMsg)||
        (msg.index != 0 && msg.index == lastMsg.index + 1)) {//
        msgList.push(msg);
        //1
        if (!roomInfo.open) {
            cache[id].unread += 1;
        }
        //callback(msg, roomInfo);
        return;
    }

    //index=0  || index
    if (msg.index == 0 || msg.index > lastMsg.index + 1) {
        msgList.push(msg);
        var params = {beginTime: lastMsg.time};
        var chat = Common.generateChat(msg);
        if(!roomInfo.pulling){
            roomInfo.pulling = true;
            roomQueryMsg(chat, params, function (data) {
                delete  roomInfo.pulling;
                insertHistoryMsgs(roomInfo,data.msgList);
                //callback(msg, roomInfo);
            },function () {
                delete  roomInfo.pulling;
                //callback(msg, roomInfo);
            });
        }
    }
}


var send = function (msg) {
    var id = Common.generateKey(msg);
    var roomInfo = cache[id];
    if(!roomInfo){
        roomInfo = {};
        roomInfo.msgList = [];
        roomInfo.unread = 0;
        roomInfo.open = true;
        cache[id] = roomInfo;
    }
    var msgList = roomInfo.msgList;
    var now = new Date().getTime();
    if (msgList.length != 0) {
        var lastMsg = msgList[msgList.length - 1];
        msg.time = now > lastMsg.time ? now : lastMsg.time+1;
        msg.index = 0;
    }else {
        msg.time = now;
    }
    msgList.push(msg);
    return roomInfo;
}

var getRoomInfo = function (session) {
    var id = Common.generateKey(session);
    return cache[id];
}

var openRoom = function (loadParams, success, fail) {
    var session = loadParams.session;
    var roomQueryMsg = loadParams.roomQueryMsg;
    var limit = loadParams.limit;

    var id = Common.generateKey(session);
    var roomInfo = cache[id];
    if (!roomInfo || !roomInfo.msgList) {
        var params = {limit: limit};
        var chat = Common.generateChat(session);
        roomQueryMsg(chat, params, function (c2c) {
            roomInfo = {};
            roomInfo.key = id;
            roomInfo.msgList = c2c.msgList||[];
            roomInfo.unread = JSON.parse(c2c.data).count;
            roomInfo.open = true;
            cache[id] = roomInfo;
            success&&success(roomInfo);
        }, function (err) {
            fail&&fail(err);
        });
    } else {
        success(roomInfo);
    }
}

var loadMoreMsg = function (loadParams, success, fail) {
    var session = loadParams.session;
    var roomQueryMsg = loadParams.roomQueryMsg;
    var endTime = loadParams.endTime;
    var limit = loadParams.limit;


    var id = Common.generateKey(session);
    var roomInfo = cache[id];
    if (!roomInfo) {
        openRoom(loadParams, success, fail);
    } else {
        if(!roomInfo.pulling){
            roomInfo.pulling = true;
            //TODO 
            //var msgList = roomInfo.msgList;
            // if(msgList&&msgList.length!=0){
            //     params.endTime = msgList[0].time;
            // }
            var params = {
                limit:limit,
                endTime:endTime
            };
            var chat = Common.generateChat(session);
            roomQueryMsg(chat,params,function (c2c) {
                var msgList = c2c.msgList||[];
                insertHistoryMsgs(roomInfo, msgList);
                delete cache[id].pulling;
                success&&success(roomInfo, msgList);
            },function (err) {
                delete cache[id].pulling;
                console.error(err);
                fail&&fail(err);
            });
        }
    }
}

exports.receive = receive;
exports.send = send;

exports.getRoomInfo = getRoomInfo;
exports.openRoom = openRoom;
exports.loadMoreMsg = loadMoreMsg;


